@import "mixin";

@media all and (max-width: 720px) {

	.bunner {
		background: $white;
		max-width: $w375;
		margin: auto;
		position: relative;
		.ninja {
				background: url("../image/Menu-1.png") center center no-repeat; }
		nav {
			height: 1533px;
			a {
				color: $white; }
			.logo {
				div {
					color: $white; } } }
		.st-effect-1.st-menu-open .st-effect-1.st-menu {
			visibility: visible;
			transform: translate3d(340%, 0, 0);
			z-index: 300; } }

	.site-h {
		margin-top: 40px; }

	.contact {
		width: $w375;
		display: block;
		margin-bottom: 15px;
		.cont, .form {
			width: 93%;
			margin: auto; } }
	.form {
		margin-bottom: 15px;
		form {
			.form-h {
				font-size: 18px; }
			input {
				width: 95%;
				margin-top: 30px;
				font-size: 16px; }
			input[type="submit"] {
				width: 95%;
				margin-top: 45px; } } }

	.map {
		width: $w375;
		height: 330px;
		margin: auto;
		// background: url("../image/Location.png") no-repeat 50%
		// background-size: 100%
		iframe {
			height: 330px !important; } } }
