@import "mixin";

@import "header";

@include site-h;


body {
	margin: 0; }
.bunner {
	border-bottom: 2px solid lighten($text, 30%); }
.st-container {
	.logo {
		div {
			color: $text; } }
	nav {
		a {
			color: $text; } } }

.contact {
	width: $width;
	max-width: 95%;
	margin: auto;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	.cont {
		width: 35%;
		.phone {
			color: $white;
			font-family: "FuturaPT";
			font-size: 24px;
			background: $green;
			margin-bottom: 20px;
			padding: 30px;
			div {
				display: inline-block;
				vertical-align: middle; }
			img {
				vertical-align: middle;
				margin-right: 20px; } }
		.phone:nth-child(2) {
			background: #f5a93d; }
		.phone:nth-child(3) {
			background: #53b7eb; } }

	.form {
		width: 64%; }
	form {
		display: inline-block;
		background: $green;
		padding: 35px 30px;
		.form-h {
			color: $white;
			font-family: "FuturaPT";
			font-size: 24px;
			span {
				text-transform: uppercase; } }
		input {
			background: none;
			border: none;
			color: $white;
			border-bottom: 1px solid $white;
			width: 46%;
			margin: 25px 10px 0 0;
			font-size: 18px; }

		input[type="text"]:nth-child(4) {
			width: 95%; }
		input[type="submit"] {
			width: 45%;
			margin-top: 178px;
			padding: 10px 0;
			border: 2px solid $white; }
		input[type="submit"]:hover,
		input[type="submit"]:focus {
			background: $white;
			color: $green; } } }

@include placeholder() {
	color: $white; }


.map {
	width: 100%;
	height: 420px;
 }	// background: url("../image/Ico-6.png") no-repeat 20% 80%

